"use client";

import { useState, useRef, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Switch } from "@/components/ui/switch";
import { Progress } from "@/components/ui/progress";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { TrafficMap } from "@/components/ui/traffic-map";
import { Footer } from "@/components/footer";
import { useYolo } from "@/hooks/use-yolo";
import { useSumo } from "@/hooks/use-sumo";

export default function TrafficManagementSystem() {
  const [aiMode, setAiMode] = useState(false);
  const [trafficLights, setTrafficLights] = useState({
    north: "red",
    south: "red",
    east: "green",
    west: "green"
  });
  const [simulationRunning, setSimulationRunning] = useState(false);
  const [detectionRunning, setDetectionRunning] = useState(false);
  const [stream, setStream] = useState<MediaStream | null>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  // YOLO hook
  const {
    isLoading,
    detectedObjects,
    error,
    isModelLoaded,
    startContinuousDetection,
    clearDetections
  } = useYolo();

  // SUMO hook
  const {
    isConnected,
    isRunning,
    currentStep,
    vehicles,
    trafficLights: sumoTrafficLights,
    metrics,
    startSimulation,
    stopSimulation,
    resetSimulation,
    error: sumoError
  } = useSumo();

  // Handle video stream lifecycle
  useEffect(() => {
    return () => {
      if (stream) {
        stream.getTracks().forEach(track => track.stop());
      }
    };
  }, [stream]);

  const toggleTrafficLight = (direction: string) => {
    if (!aiMode) {
      setTrafficLights(prev => ({
        ...prev,
        [direction]: prev[direction as keyof typeof prev] === "red" ? "green" : "red"
      }));
    }
  };

  const getTrafficLightColor = (state: string) => {
    switch (state) {
      case "red": return "bg-red-500";
      case "yellow": return "bg-yellow-500";
      case "green": return "bg-green-500";
      default: return "bg-gray-300";
    }
  };

  return (
    <>
      <div className="min-h-screen">
        {/* Hero Section */}
        <section className="hero-section">
          <div className="hero-overlay"></div>
          <div className="hero-content">
            <h1 className="text-6xl font-extrabold mb-6 gradient-text drop-shadow-lg">
              üö¶ Smart Traffic Management System
            </h1>
            <p className="text-2xl max-w-3xl mx-auto mb-10 drop-shadow-md">
              Revolutionizing urban mobility with AI-powered traffic control, YOLO detection, and SUMO simulation.
            </p>
            <button
              onClick={() => window.scrollTo({ top: 700, behavior: 'smooth' })}
              className="modern-button"
            >
              Explore Features
            </button>
          </div>
        </section>

        {/* Features Section */}
        <section className="feature-section">
          <div className="max-w-6xl mx-auto grid grid-cols-1 md:grid-cols-3 gap-10">
            <div className="feature-card">
              <h3 className="text-2xl font-semibold mb-4">AI-Powered Traffic Control</h3>
              <p className="text-gray-700 dark:text-gray-300">
                Intelligent traffic light management using real-time data and AI algorithms to optimize flow and reduce congestion.
              </p>
            </div>
            <div className="feature-card">
              <h3 className="text-2xl font-semibold mb-4">YOLO Object Detection</h3>
              <p className="text-gray-700 dark:text-gray-300">
                Real-time object detection with YOLO to monitor traffic, identify vehicles, and enhance safety measures.
              </p>
            </div>
            <div className="feature-card">
              <h3 className="text-2xl font-semibold mb-4">SUMO Traffic Simulation</h3>
              <p className="text-gray-700 dark:text-gray-300">
                Advanced traffic simulation with SUMO to analyze and predict traffic patterns for better urban planning.
              </p>
            </div>
          </div>
        </section>

        {/* Main Content Container */}
        <div className="container mx-auto px-8 py-12 max-w-7xl">
          {/* Control Panel */}
          <Card className="slide-up mb-8 mx-auto max-w-4xl modern-card">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                System Control
                <Button
                  variant="outline"
                  onClick={() => setAiMode(!aiMode)}
                >
                  {aiMode ? "Switch to Manual Mode" : "Switch to AI Mode"}
                </Button>
              </CardTitle>
              <CardDescription>
                {aiMode ? "AI is managing traffic flow automatically" : "Manual control enabled"}
              </CardDescription>
            </CardHeader>
            <CardContent>
              <Alert>
                <AlertDescription>
                  {aiMode
                    ? "ü§ñ AI system is analyzing traffic patterns and optimizing signal timing"
                    : "üë§ Manual mode: Click on traffic lights to control them manually"
                  }
                </AlertDescription>
              </Alert>
            </CardContent>
          </Card>

          <Tabs defaultValue="traffic-control" className="w-full mx-auto max-w-7xl">
            <TabsList className="grid w-full grid-cols-4 mx-auto max-w-4xl">
              <TabsTrigger value="traffic-control">üö¶ Traffic Control</TabsTrigger>
              <TabsTrigger value="yolo-detection">üëÅÔ∏è YOLO Detection</TabsTrigger>
              <TabsTrigger value="sumo-simulation">üöó SUMO Simulation</TabsTrigger>
              <TabsTrigger value="analytics">üìä Analytics</TabsTrigger>
            </TabsList>

            {/* Traffic Control Tab */}
            <TabsContent value="traffic-control" className="space-y-6 min-h-screen">
              {/* Interactive Traffic Map */}
              <TrafficMap
                trafficLights={trafficLights}
                onTrafficLightChange={toggleTrafficLight}
                aiMode={aiMode}
                vehicles={vehicles}
              />

              {/* Traditional Traffic Light Control */}
              <Card>
                <CardHeader>
                  <CardTitle>Main Intersection Control</CardTitle>
                  <CardDescription>
                    Direct control of traffic lights at the main intersection
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-3 gap-4 max-w-md mx-auto">
                    {/* North */}
                    <div className="col-start-2 text-center">
                      <Button
                        variant="outline"
                        className="w-16 h-16 rounded-full p-0"
                        onClick={() => toggleTrafficLight("north")}
                        disabled={aiMode}
                      >
                        <div className={`w-8 h-8 rounded-full ${getTrafficLightColor(trafficLights.north)}`} />
                      </Button>
                      <p className="text-sm mt-1">North</p>
                    </div>

                    {/* West */}
                    <div className="col-start-1 row-start-2 text-center">
                      <Button
                        variant="outline"
                        className="w-16 h-16 rounded-full p-0"
                        onClick={() => toggleTrafficLight("west")}
                        disabled={aiMode}
                      >
                        <div className={`w-8 h-8 rounded-full ${getTrafficLightColor(trafficLights.west)}`} />
                      </Button>
                      <p className="text-sm mt-1">West</p>
                    </div>

                    {/* Intersection */}
                    <div className="col-start-2 row-start-2 flex items-center justify-center">
                      <div className="w-16 h-16 bg-gray-200 dark:bg-gray-700 rounded-lg flex items-center justify-center">
                        <span className="text-xs">üö¶</span>
                      </div>
                    </div>

                    {/* East */}
                    <div className="col-start-3 row-start-2 text-center">
                      <Button
                        variant="outline"
                        className="w-16 h-16 rounded-full p-0"
                        onClick={() => toggleTrafficLight("east")}
                        disabled={aiMode}
                      >
                        <div className={`w-8 h-8 rounded-full ${getTrafficLightColor(trafficLights.east)}`} />
                      </Button>
                      <p className="text-sm mt-1">East</p>
                    </div>

                    {/* South */}
                    <div className="col-start-2 row-start-3 text-center">
                      <Button
                        variant="outline"
                        className="w-16 h-16 rounded-full p-0"
                        onClick={() => toggleTrafficLight("south")}
                        disabled={aiMode}
                      >
                        <div className={`w-8 h-8 rounded-full ${getTrafficLightColor(trafficLights.south)}`} />
                      </Button>
                      <p className="text-sm mt-1">South</p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            {/* YOLO Detection Tab */}
            <TabsContent value="yolo-detection" className="min-h-screen">
              <Card>
                <CardHeader>
                  <CardTitle>YOLO Object Detection</CardTitle>
                  <CardDescription>
                    Real-time traffic analysis using YOLO computer vision
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="flex space-x-4">
                    <div className="flex flex-col space-y-4 w-1/2">
                      <h3 className="text-lg font-semibold">Live Camera Feed</h3>
                      <div className="aspect-video bg-gray-200 dark:bg-gray-700 rounded-lg flex items-center justify-center">
                        <video
                          ref={videoRef}
                          className="w-full h-full object-cover rounded-lg"
                          autoPlay
                          muted
                          playsInline
                        />
                        {!stream && (
                          <div className="absolute inset-0 flex items-center justify-center bg-gray-200 dark:bg-gray-700 rounded-lg">
                            <span className="text-gray-500">üìπ Camera Feed Placeholder</span>
                          </div>
                        )}
                      </div>
                      <Button
                        className={`w-full transition-colors duration-200 ${
                          detectionRunning ? "bg-red-600 hover:bg-red-700" : "bg-blue-600 hover:bg-blue-700"
                        } text-white`}
                        onClick={async () => {
                          if (detectionRunning) {
                            // Stop detection
                            setDetectionRunning(false);
                            clearDetections();
                            if (stream) {
                              stream.getTracks().forEach(track => track.stop());
                              setStream(null);
                            }
                          } else {
                            // Start detection
                            try {
                              const mediaStream = await navigator.mediaDevices.getUserMedia({ video: true });
                              setStream(mediaStream);
                              if (videoRef.current) {
                                videoRef.current.srcObject = mediaStream;
                              }
                              setDetectionRunning(true);
                              await startContinuousDetection(videoRef.current || undefined);
                            } catch (err) {
                              console.error("Error accessing webcam:", err);
                            }
                          }
                        }}
                      >
                        {detectionRunning ? "Stop YOLO Detection" : "Start YOLO Detection"}
                      </Button>
                    </div>

                    <div className="flex flex-col space-y-4 w-1/2">
                      <h3 className="text-lg font-semibold">Detected Objects</h3>
                      <div className="space-y-2 overflow-y-auto max-h-[400px]">
                        {detectedObjects.map((obj, index) => (
                          <div key={index} className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                            <div className="flex items-center space-x-3">
                              <Badge variant="secondary">{obj.type}</Badge>
                              <span className="font-medium">{obj.count}</span>
                            </div>
                            <div className="text-sm text-gray-500">
                              {(obj.confidence * 100).toFixed(1)}% confidence
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            {/* SUMO Simulation Tab */}
            <TabsContent value="sumo-simulation" className="min-h-screen">
              <Card>
                <CardHeader>
                  <CardTitle>SUMO Traffic Simulation</CardTitle>
                  <CardDescription>
                    Simulate and analyze traffic flow patterns
                  </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="flex items-center space-x-4">
                    <Button
                      onClick={() => setSimulationRunning(!simulationRunning)}
                      variant={simulationRunning ? "destructive" : "default"}
                    >
                      {simulationRunning ? "Stop Simulation" : "Start Simulation"}
                    </Button>
                    <Badge variant={simulationRunning ? "default" : "secondary"}>
                      {simulationRunning ? "Running" : "Stopped"}
                    </Badge>
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold">Simulation View</h3>
                      <div className="aspect-video bg-gray-200 dark:bg-gray-700 rounded-lg flex items-center justify-center">
                        <TrafficMap
                          trafficLights={{
                            north: sumoTrafficLights.find(tl => tl.id === 'tl_north')?.state === 'g' ? 'green' : 'red',
                            south: sumoTrafficLights.find(tl => tl.id === 'tl_south')?.state === 'g' ? 'green' : 'red',
                            east: sumoTrafficLights.find(tl => tl.id === 'tl_east')?.state === 'g' ? 'green' : 'red',
                            west: sumoTrafficLights.find(tl => tl.id === 'tl_west')?.state === 'g' ? 'green' : 'red',
                          }}
                          vehicles={vehicles}
                          aiMode={aiMode}
                          onTrafficLightChange={() => {}}
                        />
                      </div>
                    </div>

                    <div className="space-y-4">
                      <h3 className="text-lg font-semibold">Simulation Metrics</h3>
                      <div className="space-y-3">
                        <div>
                          <div className="flex justify-between text-sm mb-1">
                            <span>Traffic Flow</span>
                            <span>75%</span>
                          </div>
                          <Progress value={75} />
                        </div>
                        <div>
                          <div className="flex justify-between text-sm mb-1">
                            <span>Average Speed</span>
                            <span>45 km/h</span>
                          </div>
                          <Progress value={60} />
                        </div>
                        <div>
                          <div className="flex justify-between text-sm mb-1">
                            <span>Queue Length</span>
                            <span>12 vehicles</span>
                          </div>
                          <Progress value={30} />
                        </div>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            {/* Analytics Tab */}
            <TabsContent value="analytics" className="min-h-screen">
              <Card>
                <CardHeader>
                  <CardTitle>Traffic Analytics</CardTitle>
                  <CardDescription>
                    Historical data and performance metrics
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <Card>
                      <CardHeader className="pb-2">
                        <CardTitle className="text-sm font-medium">Total Vehicles</CardTitle>
                      </CardHeader>
                      <CardContent>
                        <div className="text-2xl font-bold">1,234</div>
                        <p className="text-xs text-gray-500">+12% from yesterday</p>
                      </CardContent>
                    </Card>

                    <Card>
                      <CardHeader className="pb-2">
                        <CardTitle className="text-sm font-medium">Average Wait Time</CardTitle>
                      </CardHeader>
                      <CardContent>
                        <div className="text-2xl font-bold">2.3 min</div>
                        <p className="text-xs text-gray-500">-8% from yesterday</p>
                      </CardContent>
                    </Card>

                    <Card>
                      <CardHeader className="pb-2">
                        <CardTitle className="text-sm font-medium">AI Efficiency</CardTitle>
                      </CardHeader>
                      <CardContent>
                        <div className="text-2xl font-bold">94%</div>
                        <p className="text-xs text-gray-500">+3% from yesterday</p>
                      </CardContent>
                    </Card>
                  </div>

                  <div className="mt-6">
                    <h3 className="text-lg font-semibold mb-4">Setup Instructions</h3>
                    <div className="space-y-2 text-sm">
                      <p><strong>YOLO Integration:</strong> Install Python dependencies: <code className="bg-gray-100 dark:bg-gray-800 px-2 py-1 rounded">pip install ultralytics opencv-python</code></p>
                      <p><strong>SUMO Setup:</strong> Download SUMO from <code className="bg-gray-100 dark:bg-gray-800 px-2 py-1 rounded">https://eclipse.org/sumo</code> and set SUMO_HOME environment variable</p>
                      <p><strong>WebSocket:</strong> Backend server required for real-time communication with YOLO and SUMO</p>
                      <p><strong>API Integration:</strong> Configure AI service endpoints in environment variables</p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </div>
      </div>
      <Footer />
    </>
  );
}
